<template>
  <div v-if="currentProduct._id" class="edit-form">
    <h4>Edit Product</h4>
    <form>
      <div class="form-group">
        <label for="id">ID</label>
        <input
          type="text"
          class="form-control"
          id="id"
          v-model="currentProduct._id"
          readonly
        />
      </div>
      <div class="form-group">
        <label for="name">Name<span style="color: red"> *</span></label>
        <input
          type="text"
          class="form-control"
          id="name"
          v-model="currentProduct.name"
          required
        />
      </div>
      <div class="form-group">
        <label for="category">Category<span style="color: red"> *</span></label>
        <br />
        <select v-model="currentProduct.category" class="form-control" required>
          <option disabled>Choose Category</option>
          <option
            v-for="(key, index) in category"
            :key="key"
            v-bind:value="category[index].value"
          >
            {{ category[index].value }}
          </option>
        </select>
      </div>
      <div class="form-group">
        <label for="detail">Detail</label>
        <textarea
          id="detail"
          class="form-control"
          v-model="currentProduct.detail"
          name="detail"
          style="resize: none"
          rows="3"
        ></textarea>
      </div>
      <div class="form-group">
        <label for="material">Material</label>
        <input
          type="text"
          class="form-control"
          id="material"
          v-model="currentProduct.material"
        />
      </div>
      <div class="form-group">
        <label for="size">Size</label>
        <input
          type="number"
          min="0"
          step="1"
          placeholder="0"
          class="form-control"
          id="size"
          v-model="currentProduct.size"
        />
      </div>
      <div class="form-group">
        <label for="ar_images">Ar Images</label>
        <div
          :class="[
            currentProduct.ArImages && currentProduct.ArImages[0].url
              ? 'custom-file has-image-preview'
              : 'custom-file',
          ]"
        >
          <input
            type="file"
            accept="image/*"
            class="custom-file-input"
            id="ar_images"
            aria-describedby="inputGroupFileAddon01"
            name="ar_images"
            @change="onFileChangeArImage"
          />
          <label
            class="custom-file-label has-image-preview"
            for="ar_images"
            v-if="currentProduct.ArImages && currentProduct.ArImages[0].url"
          >
            <img class="preview" :src="currentProduct.ArImages[0].url" />
          </label>
          <label class="custom-file-label" for="ar_images" v-else
            >Choose file</label
          >
        </div>
      </div>
      <div class="form-group">
        <div class="form-group">
          <label for="inner_radius">Inner radius</label>
          <input
            type="number"
            class="form-control"
            id="inner_radius"
            min="0"
            step="1"
            v-model="currentProduct.ArImages[0].inner_radius"
            name="inner_radius"
          />
        </div>
      </div>
      <div class="form-group">
        <label for="images">Images</label>
        <div v-for="index in 5" :key="index">
          <div
            :class="[
              currentProduct.images[index - 1].url != ''
                ? 'input-group has-image-preview'
                : 'input-group',
            ]"
            style="margin-top: 8px"
          >
            <div class="input-group-prepend">
              <span class="input-group-text">{{ index }}</span>
            </div>
            <div class="custom-file">
              <input
                type="file"
                accept="image/*"
                class="custom-file-input"
                :id="'images' + index"
                aria-describedby="inputGroupFileAddon01"
                :name="'images' + index"
                @change="onFileChangeImages"
              />
              <label
                class="custom-file-label has-image-preview"
                :for="'images' + index"
                v-if="currentProduct.images[index - 1].url != ''"
              >
                <img
                  class="preview"
                  :src="currentProduct.images[index - 1].url"
                />
              </label>
              <label class="custom-file-label" :for="'images' + index" v-else
                >Choose file</label
              >
            </div>
          </div>
        </div>
      </div>
    </form>

    <button class="badge badge-danger mr-2" @click="deleteProduct">
      Delete
    </button>

    <button type="submit" class="badge badge-success" @click="updateProduct">
      Update
    </button>
    <p>{{ message }}</p>
  </div>

  <div v-else>
    <br />
    <p>Please click on a Product...</p>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import DataService from "@/services/DataService";
import Product from "@/types/Product";
import ResponseData from "@/types/ResponseData";
import Category from "@/types/Category";

export default defineComponent({
  name: "product",
  data() {
    return {
      currentProduct: {} as Product,
      message: "",
      category: [
        {
          _id: "",
          value: "",
          text: "",
          image: "",
        },
      ] as Category[],
      images: [{}] as Blob[],
      ArImages: [{}] as Blob[],
    };
  },
  methods: {
    retrieveCategory() {
      DataService.getAllCategory()
        .then((response: ResponseData) => {
          this.category = response.data.category;
          console.log(this.category);
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    getProduct(id: string) {
      DataService.get(id)
        .then((response: ResponseData) => {
          this.currentProduct = response.data;
          console.log(response.data);
          for (
            let index = this.currentProduct.images?.length as number;
            index < 5;
            index++
          ) {
            this.currentProduct.images?.push({
              image_id: index,
              url: "",
            });
          }
          if (this.currentProduct.ArImages === null) {
            this.currentProduct.ArImages = [{ inner_radius: 0, url: "" }];
          }
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    updateProduct() {
      if (!this.currentProduct.name || !this.currentProduct.detail) {
        this.message = "Please input Name and Category.";
        return;
      }
      let data = {
        _id: this.currentProduct._id,
        name: this.currentProduct.name,
        detail: this.currentProduct.detail,
        category: this.currentProduct.category,
        material: this.currentProduct.material,
        size: Number(this.currentProduct.size),
      };
      this.currentProduct.size = Number(this.currentProduct.size);
      this.currentProduct.ArImages
        ? (this.currentProduct.ArImages[0].inner_radius = Number(
            this.currentProduct.ArImages[0].inner_radius
          ))
        : null;
      DataService.update(data)
        .then((response: ResponseData) => {
          console.log(response.data);
          DataService.uploadImages(
            this.currentProduct._id as string,
            this.images
          )
            .then((response: ResponseData) => {
              console.log(response.data);
              DataService.uploadArImage(
                this.currentProduct._id as string,
                this.ArImages,
                this.currentProduct.ArImages
                  ? this.currentProduct.ArImages[0].inner_radius
                  : null
              )
                .then((response: ResponseData) => {
                  console.log(response.data);
                  this.message = "The product was updated successfully!";
                })
                .catch((e: Error) => {
                  console.log(e);
                });
            })
            .catch((e: Error) => {
              console.log(e);
            });
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    deleteProduct() {
      if (confirm("Are you sure you want to delete this item?")) {
        DataService.delete("product", this.currentProduct._id as string)
          .then((response: ResponseData) => {
            console.log(response.data);
            this.$router.push({ name: "products" });
          })
          .catch((e: Error) => {
            console.log(e);
          });
      }
    },

    onFileChangeArImage: function (event: Event) {
      var input = event.target as HTMLInputElement;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.currentProduct.ArImages
            ? (this.currentProduct.ArImages[0].url = reader.result as string)
            : null;
          input.files ? (this.ArImages[0] = input.files[0]) : null;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },

    onFileChangeImages: function (event: Event) {
      var input = event.target as HTMLInputElement;
      var index = (parseInt(input.name.slice(6, 7)) - 1) as number;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.currentProduct.images
            ? (this.currentProduct.images[index].image_id = index)
            : null;
          this.currentProduct.images
            ? (this.currentProduct.images[index].url = reader.result as string)
            : null;
          input.files ? (this.images[index] = input.files[0]) : null;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
  },
  mounted() {
    this.message = "";
    this.getProduct(this.$route.params.id as string);
    this.retrieveCategory();
  },
});
</script>

<style>
.edit-form {
  max-width: 300px;
  margin: auto auto 40px auto;
}
.has-image-preview {
  height: 100px;
}
.preview {
  height: 100%;
  object-fit: contain;
}
.has-image-preview .custom-file-label::after {
  content: "Change";
  height: 100px;
  display: flex;
  align-items: center;
}
.badge {
  margin-top: 12px;
  border: none;
  border-radius: 6px;
  padding: 16px 24px;
  color: black;
}
.badge-danger:hover {
  color: white;
}
.badge-success:hover {
  color: white;
}
</style>
